<template>
  <div style="margin: 32px 0;">
    <Scroll height="400px" style="margin: 32px 0;" :on-reach-bottom="handleReachBottom">
      <Card dis-hover v-for="(item, index) in pluginList" :key="index" style="margin: 32px 0">
        <p>
          <Tag style="width:100px;" color="primary">插件名</Tag>
          <strong>{{ item.name }}</strong>
        </p>
        <p>
          <Tag style="width:100px;" color="primary">UUID</Tag>
          <strong>{{ item.uuid }}</strong>
        </p>
        <p>
          <Tag style="width:100px;" color="primary">插件版本</Tag>
          <strong>{{ item.version }}</strong>
        </p>
      </Card>
    </Scroll>
  </div>
</template>
<script>
//http://localhost:2580/dashboard/plugins
let config = require("../../config/config.json");

export default {
  created() {
    this.loadData();
  },
  data() {
    return {
      pluginList: []
    };
  },
  methods: {
    handleReachBottom() {
      //return new Promise(resolve => {});
    },
    /**
      从服务器请求数据
      http://localhost:2580/dashboard/plugins


       */
    loadData() {
      let _this = this;
      this.axios
        .get(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/plugins",
          {
            params: {},
            headers: {
              web_console_token: localStorage.getItem("web_console_token")
            }
          }
        )
        .then(response => {
          console.log(response.data);
          if (response.data.state == 1) {
            _this.pluginList = response.data.data;
          } else {
            this.$Message.error("数据请求失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    }
  }
};
</script>
